<template>
  <div>
    <div class="fasilitas-form">
      <h1 class="content-header">
        {{ this.$route.name }}
      </h1>
      <base-header class="pt-4 pb-8" type="primary"></base-header>
      <div class="container-fluid mt--8">
        <div class="row card-wrapper">
          <div class="form-info col-12">
            <div class="card">
              <div class="card-header">
                <h2 class="mb-0">Form Jenis Fasilitas</h2>
              </div>
              <div class="card-body">
                <form>
                  <base-input label="Nama Jenis Fasilitas">
                    <textarea
                      v-model="form.name"
                      class="form-control"
                      id="nama-fasilitas"
                      rows="1"
                      resize="none"
                    ></textarea>
                  </base-input>
                  <div class="row">
                    <div class="col text-right">
                      <base-button type="primary" @click="submitForm"
                        >Simpan</base-button
                      >
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import Cabang from "../cabang";
import Jenis from "./jenis";
import Axios from "axios";
import Swal from "sweetalert2";
import "sweetalert2/dist/sweetalert2.css";

const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};

export default {
  components: {
    HtmlEditor,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      api: this.$store.getters.getAPI,
      cabang: Cabang,
      form: {
        name: null,
      },
    };
  },
  computed: {},
  methods: {
    getType() {
      Axios.get(
        this.$store.getters.getAPI.url + this.$store.getters.getAPI.fs.getType
      )
        .then((response) => {
          if (this.$store.getters.isDebugging) {
            console.log("DEBUG: Success to type list. Data: ", response.data);
          }
          let array = [];
          for (let i = 0; i < response.data.length; i++) {
            array.push({
              value: response.data[i].ID,
              label: response.data[i].NAME,
            });
          }
          array.sort((a, b) => {
            let aVal = a.label;
            let bVal = b.label;
            return bVal - aVal;
          });
          this.jenis = array;
          // this.renders.loading = false;
        })
        .catch((error) => {
          if (this.$store.getters.isDebugging) {
            console.log("ERROR: Failed to get rating list. ", error);
          }
        });
    },
    submitForm() {
      if (this.$store.getters.isDebugging) {
        console.log("DEBUG: Initiating data saving. Data: ", this.form);
      }
      Swal.fire({
        title: `Konfirmasi`,
        text: "Apakah anda yakin untuk menyimpan data?",
        showCancelButton: true,
        icon: "question",
      }).then((result) => {
        if (result.isConfirmed) {
          if (this.$route.params.method === "ubah") {
            console.log(
              "PUT: ",
              this.$store.getters.getAPI.url,
              this.$store.getters.getAPI.fs.putType,
              this.form.id
            );
            Axios.put(
              this.$store.getters.getAPI.url +
                this.$store.getters.getAPI.fs.putType +
                this.form.id,
              this.form
            )
              .then((response) => {
                Swal.fire("Data tersimpan", "", "success").then(() => {
                  this.getType();
                  this.$router.push("/fasilitas/daftar");
                });
              })
              .catch((error) => {
                Swal.fire("Data tidak tersimpan", "ERROR: " + error, "error");
              });
          } else {
            Axios.post(
              this.$store.getters.getAPI.url +
                this.$store.getters.getAPI.fs.postType,
              this.form
            )
              .then((response) => {
                Swal.fire("Data tersimpan", "", "success").then(() => {
                  this.$router.push("/fasilitas/daftar");
                });
              })
              .catch((error) => {
                Swal.fire("Data tidak tersimpan", "ERROR: " + error, "error");
              });
          }
        }
      });
    },
  },
  created() {
    if (this.$route.params.method === "ubah") {
      if (this.$store.getters.getUbahJenisFasilitas) {
        this.form.id = this.$store.getters.getUbahJenisFasilitas.value;
        this.form.name = this.$store.getters.getUbahJenisFasilitas.label;
      } else {
        this.$router.push("tambah");
      }
    }
  },
};
</script>

<style lang="scss">
.fasilitas-form {
  .dz-message {
    .dz-button {
      background: hsla(0, 0%, 100%, 0);
      border: none;
      color: #8898aa;
    }
  }
}
</style>
